import { DatePipe } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { CompanyPenalties } from 'app/core/models/company-penalties.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-company-penalties',
  templateUrl: './company-penalties.component.html',
  styleUrls: ['./company-penalties.component.scss']
})
export class CompanyPenaltiesComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent
  dataSource: MatTableDataSource<CompanyPenalties>;
  // displayedColumns: string[] = ['companyDescription', 'companyPenaltyAmount', 'deleteIcon'];
  displayedColumns: string[] = ['companyDescription', 'companyPenaltyAmount'];
  companyPenalties = new CompanyPenalties();
  companyPenaltiesList = [];
  companyList = [];
  companyPenaltiesForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public datepipe: DatePipe
  ) { }

  ngOnInit(): void {
    this.setFormValidator();
    this.getCompanyList();
    this.getCompanyPenaltiesList();
  }

  getCompanyPenaltiesList(): void {
    this.apiService.findAll(ENDPOINTS.companyPenalties).subscribe(
      (res: any) => {
      if (res) {
        this.companyPenaltiesList = res.responseData.data;
        this.dataSource = new MatTableDataSource<CompanyPenalties>(res.responseData.data);
        this.dataSource.paginator = this.formListComponent.paginator;
        this.dataSource.sort = this.formListComponent.sort;
      }
    });
  }

  getCompanyPenalties(): void {
    this.apiService.findById(ENDPOINTS.companyPenalties, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {

        this.companyPenalties = res.responseData.data;
        console.log(this.companyPenalties);
      }
    });
  }

  getCompanyList(): void {
    this.apiService.findAll(ENDPOINTS.activeCompanies).subscribe(
      (res: any) => {
      if (res) {
        this.companyList = res.responseData.data;
      }
    });
  }

  save(): void {
    this.formatDate();
    this.apiService.save(ENDPOINTS.companyPenalties, this.companyPenalties).subscribe(
      (res: any) => {
      if (res) {
        this.sweetAlertService.success(res);
      }else{
        this.sweetAlertService.error(res);
      }
      this.getCompanyPenaltiesList();
      this.resetFormValidator();
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.companyPenalties, this.companyPenalties).subscribe(
      (res: any) => {
      if (res) {
        this.sweetAlertService.success(res);
      }else{
        this.sweetAlertService.error(res);
      }
      this.getCompanyPenaltiesList();
      this.resetFormValidator();
    });
  }

  setFormValidator(): void {
    this.companyPenaltiesForm = this.formBuilder.group({
      company: ['', Validators.required],
      penaltyAmount: ['', Validators.required],
      // penaltyDate: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.companyPenalties = new CompanyPenalties();
    this.companyPenaltiesForm.markAsUntouched();
    this.companyPenaltiesForm.markAsPristine();
  }

  compareCompanyPenalties(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  formatDate(): void {
    // this.brandPenalties.penaltyDate = this.datepipe.transform(new Date(), 'yyyy-MM-dd')
    this.datepipe.transform(this.companyPenalties.penaltyDate, 'yyyy-MM-dd')
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this company penalty?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.companyPenalties, this.companyPenalties.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Company penalty successfully deleted.');
            this.resetFormValidator();
            this.getCompanyList();
            this.getCompanyPenaltiesList();
          }
        );
      }
    });
  }

}
